@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header {
    display: flex;
    padding: 15px 15px;
    justify-content: space-between;
    background-color: white;
}

.header__left>img {
    height: 40px;
}

.header__center {
    display: flex;
    justify-content: center;
}

.header__input {
    display: flex;
    align-items: center;
    justify-content: end;
    background-color: #f5f5f5;
    padding: 10px;
    margin-left: 10px;
    border-radius: 999px;
    outline: none;
}

.header__input:hover {
    background-color:#e5e5e5;
}

.header__input>input {
    border: none;
    background-color: transparent;
    outline-width: 0;
}

.header__info {
    display: flex;
    align-items: center;
}

.header__words {
    font-size: 17px;
    font-weight: 490;
    line-height: 24px;
    padding: 16px 12px 18px;
    text-align: center;
    cursor: pointer;
}

.header__input2 {
    display: flex;
    background-color: #f5f5f5;
    padding: 10px;
    border-radius: 999px;
    outline: none;
    border-color: transparent;
}

.loginn {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: white;
}

.material__icon:hover {
    background-color: #e5e5e5;
    border-radius: 100px;
    padding: 6px;
    cursor: pointer;
}

.button__x {
    cursor: pointer;
    border-radius: 100px;
}

.header__right {
    display: flex;
}
/* Media queries */
@media screen and (max-width: 765px) {
   .header__input {
       display: none;
   }
   
   .header__left {
       display: none;
   }
  }
.home__texts {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #f5f5f5;;
}

.home__text {
    font-size: 13px;
    padding-left: 20px;
}

.home__connect {
    display: flex;
    justify-content: center;
    font-family: 'Anton';
    font-weight: 400;
    font-size: 80px;
    line-height: 28px;
}

.home__connec {
    display: flex;
    justify-content: center;
    font-weight: 400;
    font-size: 20px;
}

.responsive {
    width: 90%;
    height: auto;
    padding-left: 70px;
    padding-right: 50px;
}


.home__p {
    display: flex;
    justify-content: center;
    font-size: 30px;
    padding-right: 180px;
}

.home__pp {
    display: flex;
    justify-content: center;
    font-weight: 400;
    font-size: 18px;
}

@media screen and (max-width: 765px) {
    .home__connect {
        text-align:inherit
    }
   }
.top {
    display: flex;
    padding: 7px;
    justify-content: space-between;
    background-color:#f5f5f5;
}

.top__left {
    padding-left: 20px;
}

.top__right {
    display: flex;
    padding: 0px 38px 0px 36px;
}

.top__help {
    display: flex;
    padding-right: 10px;
    line-height: 16px;
    font-size: 13px;
    cursor: pointer;
}

.top__help:hover {
    background: transparent;
}

/* Login styles */
.login {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login__logo {
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
  }

.login__container {
    width: 300px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;
}

.login__container > h1 {
    font-weight: 500;
    margin-bottom: 20px;
  }

.login__container > form > h5 {
    margin-bottom: 5px;
}

.login__container > form > input {
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
  }

.login__container > p {
    margin-top: 15px;
    font-size: 12px;
}

.login__signInButton {
    color: white;
    background: black;
    border-radius: 4px;
    width: 100%;
    height: 40px;
    border: 1px thin;
    margin-top: 10px;
  }

  .login__registerButton {
    width: 100%;
    height: 30px;
    padding-left: 60px;
    margin-top: 10px;
  }

  .input__email {
    border: 1px solid lightgray;
  }

  .name__account {
      padding-left: 70px;
}


